import React, { useState } from 'react';
import { withRouter } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

import {
  PHOTO_REQUIRED,
  NAME_REQUIRED,
  PHONE_REQUIRED,
  PASSWORD_REQUIRED,
  SIMPLE_PASSWORD_VALIDATION,
} from '../../../constants/errorConstants';

import { addDriverSaga } from '../../../store/actions';

const AddDriver = props => {
  const dispatch = useDispatch();
  const { open } = useSelector(state => state.modal);

  const tableFilter = {
    columnName: 'name',
    orderBy: 'asc',
    search: '',
    skip: 0,
    limit: 10,
  };

  const [photoURL1, setPhotoURL1] = useState(null);
  const [photoURL2, setPhotoURL2] = useState(null);
  const [profileimage, setprofileimage] = useState(null);

  const validationSchema = Yup.object({
    name: Yup.string().trim().required(NAME_REQUIRED),
    phone: Yup.string()
      .label('Phone')
      .trim()
      .min(10)
      .max(10)
      .matches(/^[0-9]*$/, 'Enter Valid Number')
      .required(PHONE_REQUIRED),
    frontimage: Yup.string().required(PHOTO_REQUIRED),
    backimage: Yup.string().required(PHOTO_REQUIRED),
    profileimage: Yup.string().required(PHOTO_REQUIRED),
    dob: Yup.string().required('Date of Birth Required'),
    licenseNumber: Yup.string().required('Driving Licence Number Required'),
    password: Yup.string()
      .matches(/^[A-Za-z\d@$!%*?&]{8,}$/, SIMPLE_PASSWORD_VALIDATION)
      .required(PASSWORD_REQUIRED),
  });

  /* eslint-disable no-param-reassign */
  const submitButtonHandler = values => {
    console.log(values);
    values.deviceToken = '10'; /* eslint-disable no-param-reassign */
    values.deviceType = '10'; /* eslint-disable no-param-reassign */
    dispatch(
      addDriverSaga({
        history: props.history,
        formValues: values,
        tableFilter,
      }),
    );
  };

  return (
    <>
      <CardWithBack
        pageTitle={
          props.history.location.pathname.includes('add')
            ? 'Add Driver'
            : 'Edit Driver'
        }
        dataLoadeed={typeof 1 === typeof 2}
        errorMsgAddPromoCode=""
      >
        <Formik
          initialValues={{
            name: '',
            email: '',
            phone: '',
            dob: '',
            gender: 'male',
            licenseNumber: '',
            password: '',
            frontimage: '',
            backimage: '',
            profileimage: '',
          }}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            submitButtonHandler(values);
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Full Name:</h4>
                    <input
                      type="text"
                      name="name"
                      placeholder="Enter Full Name"
                      className="form-control"
                      value={values.name}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.name && touched.name && errors.name}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4>Email : ( Optional ) </h4>
                    <input
                      type="text"
                      name="email"
                      placeholder="Enter Email"
                      className="form-control"
                      value={values.email}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.email && touched.email && errors.email}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Phone : </h4>
                    <input
                      type="text"
                      name="phone"
                      placeholder="Enter Phone Number"
                      className="form-control"
                      value={values.phone}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.phone && touched.phone && errors.phone}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-12">
                  <div className="from-group text-left">
                    <h4 className="required-field">Date of Birth : </h4>
                    <input
                      type="date"
                      name="dob"
                      value={values.dob}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control"
                      // required
                    />
                    {/* <Datepicker
                    selected={values.dob}
                    name="dob"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    className="form-control"
                  /> */}
                    <div className="error-message">
                      {errors.dob && touched.dob && errors.dob}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-12">
                  <div className="from-group text-left">
                    <h4 className="required-field">Gender : </h4>
                    <select
                      name="gender"
                      value={values.gender}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control"
                      style={{ height: '39px' }}
                    >
                      <option value="male" label="Male" />
                      <option value="female" label="Female" />
                      <option value="other" label="Other" />
                    </select>
                    <div className="error-message">
                      {errors.gender && touched.gender && errors.gender}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Driving Licence Number :{' '}
                    </h4>
                    <input
                      type="text"
                      name="licenseNumber"
                      placeholder="Enter Driving Licence Number"
                      className="form-control"
                      value={values.licenseNumber}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.licenseNumber &&
                        touched.licenseNumber &&
                        errors.licenseNumber}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Password For Driver : </h4>
                    <input
                      type="text"
                      name="password"
                      placeholder="Enter Password"
                      className="form-control"
                      value={values.password}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.password && touched.password && errors.password}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-3">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Document Picture ( Front Side) :
                    </h4>
                    <input
                      type="file"
                      name="frontimage"
                      id="frontimage"
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'frontimage',
                                e.currentTarget.files[0],
                              );
                              setPhotoURL1(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setPhotoURL1(null);
                        }
                      }}
                      // onChange={handleChange}
                      onBlur={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'frontimage',
                                e.currentTarget.files[0],
                              );
                              setPhotoURL1(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setPhotoURL1(null);
                        }
                      }}
                    />
                    {photoURL1 && (
                      <img
                        className="photo-preview"
                        alt="dishImage"
                        style={{ width: '40%' }}
                        src={photoURL1}
                      />
                    )}
                    <div className="error-message">
                      {errors.frontimage &&
                        touched.frontimage &&
                        errors.frontimage}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Document Picture (Back Side) :
                    </h4>
                    <input
                      type="file"
                      name="backimage"
                      id="backimage"
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'backimage',
                                e.currentTarget.files[0],
                              );
                              setPhotoURL2(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setPhotoURL2(null);
                        }
                      }}
                      // onChange={handleChange}
                      onBlur={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'backimage',
                                e.currentTarget.files[0],
                              );
                              setPhotoURL2(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setPhotoURL2(null);
                        }
                      }}
                    />
                    {photoURL2 && (
                      <img
                        className="photo-preview"
                        style={{ width: '40%' }}
                        alt="dishImage"
                        src={photoURL2}
                      />
                    )}
                    <div className="error-message">
                      {errors.backimage &&
                        touched.backimage &&
                        errors.backimage}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Driver Picture : </h4>
                    <input
                      type="file"
                      name="profileimage"
                      id="photo3"
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'profileimage',
                                e.currentTarget.files[0],
                              );
                              setprofileimage(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setprofileimage(null);
                        }
                      }}
                      // onChange={handleChange}
                      onBlur={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue(
                                'profileimage',
                                e.currentTarget.files[0],
                              );
                              setprofileimage(URL.createObjectURL(files[0]));
                            }
                          }
                        } else {
                          setprofileimage(null);
                        }
                      }}
                    />
                    {profileimage && (
                      <img
                        className="photo-preview"
                        style={{ width: '40%' }}
                        alt="dishImage"
                        src={profileimage}
                      />
                    )}
                    <div className="error-message">
                      {errors.profileimage &&
                        touched.profileimage &&
                        errors.profileimage}
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-group">
                <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  style={{ marginTop: '20px' }}
                >
                  Add Driver
                </button>
              </div>
            </form>
          )}
        </Formik>
      </CardWithBack>
      {open && <AlertMessageModal />}
    </>
  );
};

export default SetTokenHeader(withRouter(AddDriver), axios);
